$color-1Purple: #6e3f8d;
$color-2Purple: #744d8c;
$color-lightPurple: #e8dcf2;
$color-Yellow: #f2c204;
$color-Orange: #ce7b00;
$color-Blue: #2b82a8;
$color-lightGrey: #e8e8e8;
$color-Black: #3b3b3b;

// $color-gradation: transparent
//   linear-gradient(221deg, #6e3f8d 0%, #6e3f8d 14%, #744d8c 35%, #f2c204 100%) 0%
//   0% no-repeat padding-box;

$color-gradient: linear-gradient(45deg, $color-Yellow, $color-1Purple 70%);

$box-shadow: 5px 5px 5px #00000029;
$box-shadow-inset: inset 0px 0px 10px #ffffff29;

@mixin backColor {
}

* {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR";
}
a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  text-decoration: none;
  color: inherit;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
input:focus {
  outline: none;
}
textarea:focus {
  outline: none;
}

.home {
  header {
    height: 500px;
    width: 100%;
    background: #6e3f8d;
  }
  header::before {
    content: "";
    background-image: url("./img/people-ga1056f790_1920.jpg");
    background-size: cover;
    opacity: 0.65;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    height: 500px;
  }
  .back1 {
    height: 750px;
    width: 100%;
    background-color: #f2c204;
    position: relative;
    display: flex;
    .box1 {
      background-color: $color-lightGrey;
      position: absolute;
      width: 80vw;
      border-radius: 5px;
      right: 0;
      top: -20px;
      height: 200px;
      box-shadow: $box-shadow;
    }
    .box1-1-frame {
      right: 5vw;
      top: -50px;
      display: flex;
      justify-content: center;
      position: absolute;
      .box1-1 {
        margin-right: 5px;
        width: 180px;
        cursor: pointer;
        .orange {
          background-color: $color-Orange;
        }
        .purple {
          background-color: $color-2Purple;
        }
        .blue {
          background-color: $color-Blue;
        }
        .top {
          border-radius: 5px;
          color: white;
          font-size: 13px;
          height: 180px;
          padding-top: 120px;
          box-shadow: $box-shadow;
        }
        .orange::before {
          content: "";
          background-image: url("./img/drinks-g196571e25_1920.jpg");
          background-size: cover;
          opacity: 0.1;
          position: absolute;
          top: 0px;
          left: 0px;
          right: 0px;
          bottom: 0px;
          height: 180px;
          width: 180px;
          border-radius: 5px;
        }
        .purple::before {
          content: "";
          background-image: url("./img/people-g616738467_1920.jpg");
          background-size: cover;
          opacity: 0.1;
          position: absolute;
          top: 0px;
          left: 185px;
          right: 0px;
          bottom: 0px;
          height: 180px;
          width: 180px;
          border-radius: 5px;
        }
        .blue::before {
          content: "";
          background-image: url("./img/team-gf9694f70d_1920.jpg");
          background-size: cover;
          opacity: 0.1;
          position: absolute;
          top: 0px;
          left: 370px;
          right: 0px;
          bottom: 0px;
          height: 180px;
          width: 180px;
          border-radius: 5px;
        }
        .bottom {
          height: 5px;
          margin-top: 10px;
          border-radius: 10px;
          box-shadow: $box-shadow;
        }
      }
    }
    .box2 {
      background-color: $color-2Purple;
      width: 700px;
      border-radius: 5px;
      height: 200px;
      bottom: 0px;
      margin: auto;
      display: flex;
      position: relative;
      box-shadow: $box-shadow;
      .box2-1-frame {
        margin: auto;
        display: flex;
        position: absolute;
        top: 25px;
        left: -30px;
        .box2-1 {
          border-radius: 5px;
          background-color: $color-Black;
          margin-right: 5px;
          font-size: 15px;
          color: white;
          width: 250px;
          height: 150px;
          display: flex;
          box-shadow: $box-shadow;
          cursor: pointer;
          ul {
            z-index: 5;
            margin: auto;
            li {
            }
            .title {
            }
            .field {
              margin-top: 35px;
              font-weight: bold;
            }
          }
        }
        .first::before {
          content: "";
          background-image: url("./img/people-g9e2eac3f7_1920.jpg");
          background-size: cover;
          opacity: 0.5;
          position: absolute;
          top: 0px;
          left: 0px;
          right: 0px;
          bottom: 0px;
          width: 250px;
          height: 150px;
          border-radius: 5px;
        }
        .second::before {
          content: "";
          background-image: url("./img/team-g395942733_1920.jpg");
          background-size: cover;
          opacity: 0.5;
          position: absolute;
          top: 0px;
          left: 255px;
          right: 0px;
          bottom: 0px;
          width: 250px;
          height: 150px;
          border-radius: 5px;
        }
        .third::before {
          content: "";
          background-image: url("./img/startup-g500b59503_1920.jpg");
          background-size: cover;
          opacity: 0.5;
          position: absolute;
          top: 0px;
          left: 510px;
          right: 0px;
          bottom: 0px;
          width: 250px;
          height: 150px;
          border-radius: 5px;
        }
      }
    }
  }
  .back2 {
    height: 500px;
    width: 100%;
    background-color: #e8e8e8;
    position: relative;
    .box {
      border-radius: 5px;
      height: 350px;
      width: 75vw;
      position: absolute;
      top: -100px;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      box-shadow: $box-shadow;
      .img {
        background-image: url("./img/students-g72bb70bff_1920.jpg");
        background-size: cover;
        background-position: center;
        height: 100%;
        width: 45%;
        border-radius: 5px 0 0 5px;
      }
      .text {
        background: $color-gradient;
        height: 100%;
        width: 55%;
        border-radius: 0 5px 5px 0;
        text-align: right;
        padding: 50px 20px;
        p {
          padding: 10px;
        }
        .first {
          font-size: 15px;
          font-weight: bold;
        }
        .second {
          font-size: 15px;
        }
        .third {
          font-size: 25px;
        }
      }
    }
  }
}

.login {
  background-color: #6e3f8d;
  height: 100vh;
  display: flex;
  .login-frame {
    margin: auto;
    z-index: 5;
    background: $color-gradient;
    height: 650px;
    width: 450px;
    border-radius: 5px;
    box-shadow: $box-shadow;
    display: flex;
    box-shadow: $box-shadow-inset, $box-shadow;
    .box {
      margin: auto;
      height: 80%;
      width: 80%;
      // background-color: #2b82a8;
      span {
        display: block;
        margin: 30px;
      }
      .title {
        color: white;
        font-weight: bold;
        font-size: 35px;
      }
      .text {
        color: $color-Black;
        font-size: 35px;
        font-weight: bold;
      }
      .btns {
        margin-top: 20%;
        div {
          border-radius: 5px;
          margin: 10px auto;
          width: 75%;
          cursor: pointer;
          box-shadow: $box-shadow;
          img {
            width: 100%;
          }
        }
      }
    }
  }
}
.login::before {
  content: "";
  background-image: url("./img/people-ga1056f790_1920.jpg");
  background-size: cover;
  opacity: 0.5;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 100vh;
}
.mypage {
  background-color: #6e3f8d;
  height: 1250px;
  display: flex;
  .frame {
    z-index: 5;
    border-radius: 5px;
    box-shadow: $box-shadow;
    margin: 125px auto;
    height: 900px;
    width: 800px;
    background-color: $color-lightGrey;
    .info {
      margin: 10px;
      height: 250px;
      display: flex;
      .profile {
        width: 30%;
        img {
          width: 200px;
          display: block;
          margin: auto;
          border-radius: 5px;
          box-shadow: $box-shadow;
        }
        button {
          border: none;
          background-color: $color-Yellow;
          border-radius: 5px;
          color: white;
          font-size: 13px;
          width: 150px;
          height: 30px;
          box-shadow: $box-shadow;
          margin-top: 10px;
        }
      }
      .myinfo {
        width: 70%;
        background-color: $color-Black;
        border-radius: 5px;
        opacity: 0.8;
        display: flex;
        box-shadow: $box-shadow;
        .text {
          z-index: 5;
          // background-color: #2b82a8;
          margin: auto 20px;
          height: 100%;
          width: 100%;
          display: flex;
          table {
            margin: auto 30px;
            tr {
              font-size: 15px;
              td {
                line-height: 85%;
              }
              .title {
                height: 20px;
                color: white;
              }
              .info {
                background-color: white;
                border-radius: 5px;
                height: 25px;
                padding: 5px;
                width: 250px;
                box-shadow: $box-shadow;
              }
            }
            .nickname {
              .info {
                position: relative;
                padding: 0;
                input {
                  height: 100%;
                  border-radius: 5px;
                  width: 160px;
                }
                input::placeholder {
                  padding: 2px;
                  padding-left: 5px;
                }
                button {
                  position: absolute;
                  top: 2px;
                  right: 3px;
                  border: none;
                  background-color: $color-Yellow;
                  border-radius: 5px;
                  color: white;
                  font-size: 13px;
                  width: 80px;
                  height: 20px;
                  box-shadow: $box-shadow;
                }
              }
            }
          }
        }
      }
    }
    .group {
      display: flex;
      justify-content: space-between;
      margin: 50px 10px;
      font-size: 15px;
      .schedule {
        .title {
          margin: 10px 0;
        }
        .box {
          width: 200px;
          padding: 10px;
          .m-frame {
            .ym {
              text-align: left;
            }
            .group-frame {
              margin: auto;
              text-align: left;
              display: flex;
              align-items: center;
              margin: 10px auto;
              .date {
                font-size: 13px;
                margin-right: 10px;
              }
              .group {
                margin: 0;
                background-color: $color-1Purple;
                display: block;
                box-shadow: $box-shadow;
                border-radius: 5px;
                padding: 10px;
                font-size: 13px;
                cursor: pointer;
                span {
                }
                .name {
                }
                .field {
                }
              }
            }
          }
        }
      }
      .join {
        .title {
          margin: 10px 0;
        }
        .box {
          width: 250px;
          text-align: left;
          padding: 10px;
          .group {
            background-color: $color-Yellow;
            display: block;
            border-radius: 5px;
            box-shadow: $box-shadow;
            margin: 0;
            margin-top: 10px;
            padding: 10px;
            color: black;
            cursor: pointer;
            .name {
              margin-bottom: 5px;
            }
            .field {
              font-weight: bold;
            }
          }
        }
      }
      .bookmark {
        .title {
          margin: 10px 0;
        }
        .box {
          width: 250px;
          text-align: left;
          // height: 500px;
          padding: 10px;
          .group {
            background-color: #2b82a8;
            display: block;
            border-radius: 5px;
            box-shadow: $box-shadow;
            margin: 0;
            margin-top: 10px;
            padding: 10px;
            cursor: pointer;
            .name {
              margin-bottom: 5px;
            }
          }
        }
      }
      .box {
        background-color: $color-Black;
        border-radius: 5px;
        opacity: 0.8;
        box-shadow: $box-shadow;
        color: white;
      }
    }
    .recommend {
      margin: 50px 10px;
      text-align: left;
      .title {
        margin: 10px 20px;
      }
      .box {
        width: 100%;
        display: flex;
        justify-content: space-around;
        .group {
          width: 180px;
          background-color: $color-Orange;
          display: block;
          border-radius: 5px;
          box-shadow: $box-shadow;
          margin: 0;
          padding: 10px;
          cursor: pointer;
          .name {
            margin-bottom: 5px;
          }
          .field {
            font-weight: bold;
          }
        }
      }
    }
  }
}
.mypage::before {
  content: "";
  background-image: url("./img/people-ga1056f790_1920.jpg");
  background-size: contain;
  opacity: 0.5;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 1250px;
}

.post {
  background-color: #6e3f8d;
  height: 100vh;
  display: flex;
  .post-box {
    z-index: 5;
    margin: 50px auto;
    background-color: $color-lightGrey;
    height: 900px;
    width: 95%;
    border-radius: 5px;
    box-shadow: $box-shadow;
    .top {
      width: 85%;
      margin: 50px auto;
      justify-content: space-evenly;
      .tag {
        button {
          border: none;
          background-color: $color-Yellow;
          border-radius: 5px;
          color: white;
          font-size: 13px;
          width: 150px;
          height: 30px;
          box-shadow: $box-shadow-inset, $box-shadow;
          margin: 10px;
        }
      }
      .head {
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        .title {
          span {
            display: block;
          }
          .first {
            font-size: 20px;
          }
        }
        .search {
          position: relative;
          input {
            border: none;
            border-radius: 5px;
            height: 35px;
            width: 250px;
            padding: 10px;
            padding-right: 30px;
          }
          label {
            cursor: pointer;
            position: absolute;
            right: 5px;
            top: 5px;
          }
        }
      }
    }
    .contents {
      table {
        width: 95%;
        margin: auto;
        hr {
          margin: 0;
        }
        tr {
          display: flex;
          justify-content: space-between;
          font-size: 15px;
          td {
            width: 100%;
            padding: 5px;
          }
          .no {
            font-size: 13px;
            width: 5%;
          }
          .title {
            width: 55%;
            cursor: pointer;
          }
          .id {
            width: 20%;
          }
          .date {
            font-size: 13px;
            width: 20%;
          }
        }
        .head {
        }
        .content {
          .no {
            color: $color-Black;
          }
          .date {
            color: $color-Black;
          }
        }
      }
    }
    .bottom {
      position: relative;
      width: 95%;
      margin: auto;
      .btns {
        width: 50%;
        margin: auto;
        margin-top: 10px;
        button {
          border: none;
          background-color: inherit;
          margin: 5px;
          font-size: 15px;
        }
      }
      .writeBtn {
        position: absolute;
        top: 2px;
        right: 10px;
        border: none;
        background-color: $color-1Purple;
        border-radius: 5px;
        color: white;
        font-size: 13px;
        width: 100px;
        height: 30px;
        box-shadow: $box-shadow-inset, $box-shadow;
      }
    }
  }
}
.post::before {
  content: "";
  background-image: url("./img/people-ga1056f790_1920.jpg");
  background-size: contain;
  opacity: 0.5;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 100vh;
}

.writepost {
  background-color: #6e3f8d;
  height: 100vh;
  display: flex;
  .writepost-box {
    z-index: 5;
    margin: 50px auto;
    background-color: $color-lightGrey;
    height: 800px;
    width: 95%;
    border-radius: 5px;
    box-shadow: $box-shadow;
    form {
      width: 90%;
      margin: 25px auto;
      .input-box {
        background-color: white;
        border-radius: 5px;
        hr {
          width: 95%;
          margin: auto;
        }
        input {
          border-radius: 5px;
          width: 100%;
          border: none;
          padding: 10px 30px;
          margin: 10px auto;
          margin-top: 25px;
          font-size: 25px;
        }
        textarea.content {
          font-size: 15px;
          border-radius: 5px;
          width: 100%;
          border: none;
          padding: 10px 30px;
          margin: 10px auto;
          resize: none;
        }

        input::placeholder {
          font-size: 25px;
        }
        textarea::placeholder {
          font-size: 15px;
        }
      }
      .btn {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding: 20px;
        button {
          display: block;
          border: none;
          background-color: $color-1Purple;
          border-radius: 5px;
          color: white;
          font-size: 13px;
          width: 100px;
          height: 30px;
          box-shadow: $box-shadow-inset, $box-shadow;
        }
      }
    }
  }
}
.writepost::before {
  content: "";
  background-image: url("./img/people-ga1056f790_1920.jpg");
  background-size: contain;
  opacity: 0.5;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 100vh;
}

.postdetail {
  background-color: #6e3f8d;
  // height: 100vh;
  display: flex;
  .postdetail-box {
    z-index: 5;
    margin: 50px auto;
    background-color: $color-lightGrey;
    // height: 800px;
    width: 95%;
    border-radius: 5px;
    box-shadow: $box-shadow;
    text-align: left;
    hr {
      margin: 0 auto;
    }
    .content {
      background-color: white;
      border-radius: 5px;
      padding: 25px;
      .title {
        font-size: 25px;
        margin: 20px 0 10px 0;
      }
      .postinfo {
        span {
          margin-right: 10px;
          font-size: 13px;
        }
        .date {
          color: $color-Black;
        }
      }
      hr {
        margin: 20px 0 20px 0;
      }
    }
    .comments {
      padding: 25px;
      hr {
        margin: 10px 0 10px 0;
      }
      span {
        margin-right: 10px;
        font-size: 13px;
      }
      .date {
        color: $color-Black;
      }
    }
    form {
      width: 100%;
      display: flex;
      flex-direction: column;
      textarea {
        font-size: 15px;
        width: 95%;
        border: none;
        padding: 10px 30px;
        margin: 10px auto;
        resize: none;
        border-radius: 5px;
        box-shadow: $box-shadow;
      }
      textarea:focus {
        outline: none;
      }
      textarea::placeholder {
        font-size: 15px;
      }
      .btn {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding-right: 20px;
        button {
          display: block;
          border: none;
          background-color: $color-1Purple;
          border-radius: 5px;
          color: white;
          font-size: 13px;
          width: 100px;
          height: 30px;
          box-shadow: $box-shadow-inset, $box-shadow;
        }
      }
    }
  }
}
.postdetail::before {
  content: "";
  background-image: url("./img/people-ga1056f790_1920.jpg");
  background-size: contain;
  opacity: 0.5;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 100vh;
}

.App {
  .nav {
    margin: 10px 20px;
    background-color: transparent;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    color: white;
    z-index: 5;
    ul {
      margin-right: 40px;
      list-style: none;
      li {
        display: inline;
        margin-left: 10px;
      }
    }
  }

  footer {
    background-color: $color-1Purple;
    color: white;
    padding: 50px;
    .link {
      font-size: 13px;
      margin-bottom: 30px;
      a {
        margin: 0 15px;
      }
    }
    .sentences {
      font-size: 13px;
      margin-bottom: 30px;
    }
    .snsBtns {
      display: flex;
      justify-content: center;
    }
    .sns {
      margin: 0 15px;
    }
  }
}
